// 导入 eventBus 模块
import bus from './eventBus';
import { getUserPlaylist, getUserAudio, getSongListSongs } from '@/request/api/personalHome';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 用户 id
      userId: 0,
      // 用户昵称
      userNickname: '',
      // 当前 tab，默认是创建的歌单(1)
      personalSongListActive: 1,
      // 用户创建和收藏的歌单
      playlist: [],
      // 用户创建的歌单
      userCreateLists: [],
      // 用户收藏的歌单
      userCollectLists: [],
      // 用户创建的电台/播客
      userDjRadios: [],
      // 是否有创建的电台/播客
      isDjRadio: 0,
      // 排列模式
      modes: [{
        id: 1,
        icon: '#icon-caidan',
        title: '大图模式'
      }, {
        id: 2,
        icon: '#icon-liebiaomoshi',
        title: '列表模式'
      }, {
        id: 3,
        icon: '#icon-shituliebiaoList',
        title: '图列模式'
      }],
      // 当前排列模式(大图、列表、图列模式)
      nowArrange: 1,
      // 页面展示的歌单列表
      showingLists: [],
      // 创建的歌单图列模式每歌单前十首
      showingTenSongs1: [],
      // 收藏的歌单图列模式每歌单前十首
      showingTenSongs2: []
    };
  },

  // 写在 created 阶段（现成的解决方案）
  created() {
    // 接收从兄弟组件 PersonalInfo 传过来的用户昵称
    bus.on('userNickname', val => {
      this.userNickname = val;
    });
  },

  mounted() {
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)
    this.userId = this.$route.query.userId; // 获取用户歌单

    this.getUserPlaylist(); // 获取用户创建的电台/播客

    this.getUserAudio();
  },

  methods: {
    // 获取用户歌单(包括用户创建和收藏的)(不获取私密歌单)
    async getUserPlaylist() {
      const res = await getUserPlaylist(this.userId); // console.log('获取用户歌单', res);
      // 用户没有歌单

      if (res.data.playlist.length == 0) {
        this.isLoading = 0;
      } else {
        // 用户有歌单
        this.playlist = res.data.playlist; // console.log('用户创建和收藏的歌单', this.playlist);
        // 分离创建和收藏的歌单，一秒后才从 PersonalInfo 组件传来 userNickname，所以设置延时器

        setTimeout(() => {
          this.userCreateLists = this.playlist.filter(item => item.creator.nickname == this.userNickname); // console.log('用户创建的歌单', this.userCreateLists);

          this.showingLists = this.userCreateLists;
          this.userCollectLists = this.playlist.filter(item => item.creator.nickname !== this.userNickname); // console.log('用户收藏的歌单', this.userCollectLists);
          // if (this.userCreateLists.length !== 0) this.isLoading = 0

          this.isLoading = 0;
        }, 1010);
      }
    },

    // 获取用户创建的电台/播客
    async getUserAudio() {
      const res = await getUserAudio(this.userId); // console.log('获取用户创建的电台', res);

      if (res.data.count > 0) {
        this.isDjRadio = 1;
        this.userDjRadios = res.data.djRadios; // console.log('用户创建的电台', this.userDjRadios);
      }
    },

    // 选择当前 Tab
    tabExclusive(n) {
      this.personalSongListActive = n; // this.isLoading = 1
    },

    // 去往歌单详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    },

    // 歌单显示播放键
    playKeyMouseenter(type, index) {
      if (type == 1) this.$refs.playKeyCreate[index].classList = 'playKey';
      if (type == 2) this.$refs.playKeyCollect[index].classList = 'playKey';
    },

    // 歌单隐藏播放键
    playKeyMouseleave(type, index) {
      if (type == 1) this.$refs.playKeyCreate[index].classList = 'playKey1';
      if (type == 2) this.$refs.playKeyCollect[index].classList = 'playKey1';
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 更换排列方式
    changeArrange(type) {
      this.nowArrange = type;
    },

    // 鼠标移入排列
    arrangeMouseenter(id) {
      // 查看有 arrangeActive 类，classList[] 是伪数组，先用 Array.from 转为真数组，再用 find() 查找数组元素
      const isActive = Array.from(this.$refs.arrange[id - 1].classList).find(item => item == 'arrangeActive');
      if (!isActive) this.$refs.arrange[id - 1].classList.add('arrangeMouse');
    },

    // 鼠标移出排列
    arrangeMouseleave(id) {
      const isActive = Array.from(this.$refs.arrange[id - 1].classList).find(item => item == 'arrangeActive');
      if (!isActive) this.$refs.arrange[id - 1].classList.remove('arrangeMouse');
    },

    // 格式化单曲序号
    formatIndex(index) {
      return this.$formatIndex(index);
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    },

    // 图列模式前十首处理
    async tenSongsHandle(arrange) {
      // arrange = 1 创建的歌单，arrange = 2 收藏的歌单 ！只在第一次获取歌单歌曲
      if (arrange == 1 && this.showingTenSongs1.length == 0) {
        // 获取所有歌单的歌曲
        for (var i = 0; i < this.showingLists.length; i++) {
          const res = await getSongListSongs(this.showingLists[i].id, sessionStorage.getItem("cookie")); // 截取前十首

          this.showingTenSongs1.push(res.data.songs.slice(0, 10));
        } // console.log('showingTenSongs1', this.showingTenSongs1);

      }

      if (arrange == 2 && this.showingTenSongs2.length == 0) {
        for (var i = 0; i < this.showingLists.length; i++) {
          const res = await getSongListSongs(this.showingLists[i].id, sessionStorage.getItem("cookie"));
          this.showingTenSongs2.push(res.data.songs.slice(0, 10));
        }
      }
    }

  },
  watch: {
    // 当前 tab
    personalSongListActive() {
      if (this.personalSongListActive == 1) {
        // 展示创建的歌单
        this.showingLists = this.userCreateLists;
        if (this.nowArrange == 3) this.tenSongsHandle(1);
      }

      if (this.personalSongListActive == 2) {
        // 展示收藏的歌单
        this.showingLists = this.userCollectLists;
        if (this.nowArrange == 3) this.tenSongsHandle(2);
      }
    },

    // 当前排列
    nowArrange() {
      // 图列模式前十首处理
      if (this.nowArrange == 3) this.tenSongsHandle(this.personalSongListActive);
    },

    // 监听路由参数 $route，路由参数改变则重新请求数据 和页面重新渲染
    '$route': function (newRoute, oldRoute) {
      // console.log('newRoute:', newRoute, 'oldRoute:', oldRoute);
      // console.log('newRoute:', newRoute.query.userId, 'oldRoute:', oldRoute.query.userId);
      this.userId = newRoute.query.userId;
      this.isLoading = 1; // 重新获取用户歌单

      this.getUserPlaylist(); // 重新获取用户创建的电台/播客

      this.getUserAudio();
    }
  }
};