  mounted() {
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)
    this.userId = this.$route.query.userId; // 获取用户详情

    if (this.userId !== 0) this.getUserDetail(this.userId); // 是当前登录的用户

    if (this.userId == JSON.parse(sessionStorage.getItem('userInfo')).userid) this.isLoginedUser = 1; // 给兄弟组件 PersonalSongList 组件传用户昵称

    setTimeout(() => bus.emit('userNickname', this.profile.nickname), 1000);
  },

  methods: {
    // 利用用户 id 获取用户详情
    async getUserDetail(uid) {
      const res = await getUserDetail(uid, sessionStorage.getItem("cookie")); // console.log('获取用户详情', res);

      this.level = res.data.level || 0;
      this.profile = res.data.profile; // console.log('profile', this.profile);
      // 得到有文字描述的身份标识并分类

      if (this.profile.allAuthTypes) this.authTypes = this.profile.allAuthTypes.filter(item => item.desc !== '');
      this.authTypes10 = this.authTypes.filter(item => item.type <= 10);
      this.authTypes200 = this.authTypes.filter(item => item.type >= 200 && item.type <= 310);
      this.authTypes300 = this.authTypes.filter(item => item.type > 310 && item.type < 400);
      this.authTypesOthers = this.authTypes.filter(item => item.type > 10 && item.type < 200 || item.type >= 400); // console.log('身份标识：', this.authTypes);
    },

    // 省和城市的 id
    checkPlace(type, id) {
      if (type == 1) {
        // 找省，find() 返回数组中第一个满足要求的元素，否则返回 undefined
        const result = this.provinces.find(item => item.id == id);
        if (result) return result.name;
      } else {
        // 找城市
        const result = this.citys.find(item => item.id == id);
        if (result) return result.name;
      }
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    },

    // 去往用户动态页
    goTrends(id, nickname, limit) {
      this.$router.push({
        path: '/Trends',
        query: {
          id: id,
          nickname: nickname,
          limit: limit
        }
      });
    },

    // 判断用户有无开查看权限
    async isOpenFollows(id, type, nickname, limit) {
      // 先判断该用户有无开查看权限
      if (type == 1) {
        const res = await getUserFollows(id, limit, sessionStorage.getItem("cookie")); // console.log('获取用户关注', res);

        if (res.data.message == '用户隐私无权限查看') {
          alert('主人关闭了权限');
        } else {
          this.goFollows(id, type, nickname, limit);
        }
      }

      if (type == 2) {
        const res = await getUserFolloweds(id, limit, sessionStorage.getItem("cookie")); // console.log('获取用户粉丝', res);

        if (res.data.message == '用户隐私无权限查看') {
          alert('主人关闭了权限');
        } else {
          this.goFollows(id, type, nickname, limit);
        }
      }
    },

    // 去往用户关注/粉丝页
    goFollows(id, type, nickname, limit) {
      this.$router.push({
        path: '/Follows',
        query: {
          id: id,
          // 1关注 2粉丝
          type: type,
          nickname: nickname,
          limit: limit
        }
      });
    },

    // 去往编辑个人信息页
    goEditInfor() {
      // 是当前登录的用户
      if (this.isLoginedUser) this.$router.push('/EditInfor');
    }

  },
  watch: {
    // 监听路由参数 $route，路由参数改变则重新请求数据 和页面重新渲染
    '$route': function (newRoute, oldRoute) {
      // console.log('newRoute:', newRoute, 'oldRoute:', oldRoute);
      // console.log('newRoute:', newRoute.query.userId, 'oldRoute:', oldRoute.query.userId);
      this.userId = newRoute.query.userId; // 重新获取用户详情

      this.getUserDetail(this.userId); // 给兄弟组件 PersonalSongList 组件传用户昵称

      setTimeout(() => bus.emit('userNickname', this.profile.nickname), 1000);
    }
